.about {
	padding-top: 150px;
	padding-bottom: 122px;

	@media (max-width: $tablet-width) {
		padding-top: 56px;
		padding-bottom: 27px;

	}

	@media (max-width: $mobile-width) {
		padding-bottom: 35px;
	}
}

.about__title {
	margin: 0;
	margin-bottom: 50px;
	text-align: center;
	font-weight: 600;
	font-size: 30px;
	line-height: 30px;
	color: $title-color;
	letter-spacing: -0.005em;

	@media (max-width: $tablet-width) {
		margin-bottom: 35px;
		font-size: 24px;
	}
}

.about__list {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-template-rows: 1fr;
	width: 1202px;
	list-style: none;
	margin: 0 auto;
	padding: 0;
	list-style-type: none;
	counter-reset: item;

	@media (max-width: 1200px) {
		max-width: 588px;
		grid-template-columns: 1fr 1fr;
		grid-template-rows: 1fr 1fr;
	}

	@media (max-width: $mobile-width) {
		width: auto;
		display: block;
	}
}

.about__item {
	display: flex;
	flex-direction: column;
	align-items: center;
  margin-bottom: 30px;

	@media (max-width: $tablet-width) {
		margin-bottom: 43px;
	}

	@media (max-width: $mobile-width) {
		margin-bottom: 37px;
	}
}

.about__item:nth-child(-n + 9)::before {
	content: '0' counter(item);
	counter-increment: item;
	margin-bottom: 15px;
	font-weight: bold;
	font-size: 28px;
	line-height: 38px;
	letter-spacing: -1.27273px;
	color: $accent;

	@media (max-width: $mobile-width) {
		margin-bottom: 17px;
	}
}

.about__item:nth-child(n + 10)::before {
	content: counter(item);
	counter-increment: item;
	margin-bottom: 15px;
	font-weight: bold;
	font-size: 28px;
	line-height: 38px;
	letter-spacing: -1.27273px;
	color: $accent;

	@media (max-width: $mobile-width) {
		margin-bottom: 17px;
	}
}

.about__text {
	width: 263px;
	margin: 0;
	font-size: 22px;
	line-height: 34px;
	text-align: center;
	letter-spacing: -0.5px;

	@media (max-width: $mobile-width) {
		font-size: 18px;
		line-height: 34px;
	}
}
